// React
import React, { useState } from 'react'
// Icon Library
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// CSS
import '../App.css'

// 設定頁面組件
function Setting(props) {
  const [secretKey, setSecretKey] = useState('')
  function secretPassage(event) {
    event.preventDefault()
    console.log(secretKey)
    event.preventDefault()
    // 獲取當前網址的SearchParams
    const searchParams = new URLSearchParams(window.location.search)
    // 將輸入值作為page參數的值
    searchParams.set('page', secretKey)
    // 將網址設定為跳轉到指定頁面
    const redirectTo =
      window.location.origin +
      window.location.pathname +
      '?' +
      searchParams.toString()
    window.location.href = redirectTo
  }
  function handleInputChange(event) {
    setSecretKey(event.target.value)
  }
  function openRepProb() {
    props.setReportProblemActive(true)
  }
  return (
    <div
      id="Setting"
      className={`${props.theme}${props.theme && props.settingPage ? ' ' : ''}${
        props.settingPage ? 'open' : ''
      }`}>
      <div
        id="closeSetting"
        className={`${props.settingPage ? 'open' : ''}`}
        onClick={() => props.setSettingPage(false)}>
        <FontAwesomeIcon icon="fa-solid fa-xmark" />
      </div>
      <div
        id="openRepProb"
        className={`${props.settingPage ? 'open' : ''}`}
        onClick={() => props.setReportProblemActive(true)}>
        建議與問題回報
      </div>
      <div id="settingHeader">
        <h1>設定</h1>
      </div>
      <div id="secretPassage">
        <form id="secretPassageForm" onSubmit={secretPassage}>
          <input
            type="text"
            pattern="[A-Za-z0-9]+"
            placeholder="輸入秘密金鑰"
            value={secretKey}
            onChange={handleInputChange}
          />
          <br />
          <button className="" type="submit">
            <FontAwesomeIcon icon="fa-solid fa-arrow-right" />
          </button>
        </form>
      </div>
      <div className={`settingView ${props.settingPage ? 'open' : ''}`}>
        <div className="setBlock">
          <div className="setName">
            <h3>主題模式</h3>
          </div>
          <div className="set">
            <div
              id="prefersMode"
              className={`${props.menuActive} ${
                props.settingPage ? '' : 'close'
              }`}
              onClick={props.handleThemeChange}>
              <div className="slider">
                <span>{props.modeValue}</span>
              </div>
            </div>
          </div>
        </div>
        {/* Chat */}
        {/* <div className="setBlock">
          <div className="setName">
            <h3>Chat (Beta)</h3>
          </div>
          <div className="set" onClick={props.betaModeChange}>
            <div id="betaMode" className={`${props.betaVersion ? 'open' : ''}`}>
              <div className="slider">
                <span>{props.betaVersion ? '開啟' : '關閉'}</span>
              </div>
            </div>
          </div>
        </div> */}
      </div>
    </div>
  )
}

export default Setting
