// CSS
import '../App.css'
// DataBase
import { ServiceData, ToolsData } from '../AppData/AppData.js'

function Service(props) {
  return (
    <main
      id="folder"
      className={`${props.theme}${props.theme && props.settingPage ? ' ' : ''}${
        props.settingPage ? 'settingOpen' : ''
      }`}>
      <div id="folder-view">
        <div id="folder-view-content">
          <h1>服務</h1>
          <section className="folder-block">
            {ServiceData.map((ServiceData, index) => (
              <div
                className={ServiceData.classbackground}
                style={{
                  backgroundImage: `url(${process.env.PUBLIC_URL}${ServiceData.stylebackground})`,
                }}
                onClick={() => props.pagelink(ServiceData.link)}
                key={index}>
                <div
                  className="space"
                  style={{ height: ServiceData.divHight }}></div>
                <h1>{ServiceData.ServiceName}</h1>
                <span>{ServiceData.content}</span>
              </div>
            ))}
          </section>
          <h1>工具</h1>
          <section className="folder-block">
            {ToolsData.map((ToolsData, index) => (
              <div
                className={ToolsData.classbackground}
                style={{ backgroundImage: ToolsData.stylebackground }}
                key={index}>
                <div
                  className="space"
                  style={{ height: ToolsData.divHight }}></div>
                <h1>{ToolsData.ServiceName}</h1>
                <span>{ToolsData.content}</span>
              </div>
            ))}
          </section>
        </div>
      </div>
    </main>
  )
}

export default Service
