// CSS
import '../App.css'
// DataBase
import { PostData } from '../AppData/AppData'

function Post(props) {
  return (
    <main
      id="post"
      className={`${props.theme}${props.theme && props.settingPage ? ' ' : ''}${
        props.settingPage ? 'settingOpen' : ''
      }`}>
      <div id="post-view">
        <div id="post-view-content">
          <h1>校務公告</h1>
          <section className="post-block">
            {PostData.map((PostData) => (
              <div
                className={PostData.class}
                style={{ backgroundImage: PostData.style }}>
                <h5>{PostData.time}</h5>
                <h1>{PostData.postTitle}</h1>
                <span>{PostData.content}</span>
              </div>
            ))}
          </section>
        </div>
      </div>
    </main>
  )
}

export default Post
